<script>
// import '../firebase'

// import { getDatabase, ref, set, onValue } from "firebase/database";
import TwCitySelector from 'tw-city-selector/dist/tw-city-selector.js';



export default {
  props: ['subTotal', 'shipping'],
  components: {
  },
  data(){
    return {
      userAddress: {
        'firstName': '',
        'lastName': '',
        'county': '',
        'district': '',
        'zipCode': '',
        'address': '',
        'email': '',
        'phone': '',
      },
      firstNameInvalid: false,
      lastNameInvalid: false,
      countyInvalid: false,
      districtInvalid: false,
      addressInvalid: false,
      emailInvalid: false,
      phoneInvalid: false,
      scrollTop: document.documentElement.scrollTop,
    };
  },
  methods: {
    resetAddress(){
      this.userAddress.firstName = ''
      this.userAddress.lastName = ''
      this.userAddress.county = ''
      this.userAddress.district = ''
      document.getElementById('zipCode').value = '';
      this.userAddress.address = ''
      this.userAddress.email = ''
      this.userAddress.phone = ''
    },
    checkAddress(){
      this.firstNameInvalid = false;
      this.lastNameInvalid = false;
      this.countyInvalid = false;
      this.districtInvalid = false;
      this.addressInvalid = false;
      this.emailInvalid = false;
      this.phoneInvalid = false;

      if(this.userAddress.firstName == '') {
        this.firstNameInvalid = true;
      } else if (this.userAddress.lastName == '') {
        this.lastNameInvalid = true;
      } else if (this.userAddress.county == '') {
        this.countyInvalid = true;
      } else if (this.userAddress.district == '') {
        this.districtInvalid = true;
      } else if (this.userAddress.address == '') {
        this.addressInvalid = true;
      } else if (this.userAddress.email == '') {
        this.emailInvalid = true;
      } else if (this.userAddress.phone == '') {
        this.phoneInvalid = true;
      } else {
        let fullAddress = 
        document.getElementById('zipCode').value + ' ' +
        this.userAddress.county + 
        this.userAddress.district + 
        this.userAddress.address + ', ' +
        this.userAddress.firstName + ' ' + this.userAddress.lastName + ', ' + 
        this.userAddress.email + ', ' + this.userAddress.phone

        this.orderInfo = fullAddress + ', 總計：' + this.subTotalSum;
        console.log(this.orderInfo)
      }
    },
    autoScroller(){
      //設定進到這個組件自動跳至距離頂部600像素位置
      document.documentElement.scrollTop = 600;
    },
  },
  mounted(){
    new TwCitySelector({
      el: '.billingAddress',
      elCounty: '#county',
      elDistrict: '#district',
      elZipcode: '#zipCode' ,
      hasZipcode: true
    });

    this.autoScroller();  
  },
};
</script>

