<template>
  <Header></Header>

  <!-- 利用是否有orderInfo(結帳資料)來判斷要顯示的組件是shoppingCart或是checkOut -->
  <div class="shoppingCart" v-if="orderInfo == ''">
    <div class="container">
      <div class="big-title-box">
        <h1 class="title">shoppingCart</h1>
        <span>home&nbsp; >  &nbsp;shoppingCart</span>
      </div>
      <div class="stepStatusBox">
        <ul class="stepStatus">
          <li class="step finish">
            <div class="circle">01</div>
            <div class="title">shopping cart</div>
          </li>
          <li class="line"></li>
          <li class="step">
            <div class="circle">02</div>
            <div class="title">check out</div>              
          </li>
          <li class="line"></li>
          <li class="step">
            <div class="circle">03</div>
            <div class="title">order complete</div>              
          </li>
        </ul>
      </div>
      <div class="cartList">
        <div class="titleBox">
          <div class="halfBox">
            <div class="title">product</div>
          </div>
          <div class="price">price</div>
          <div class="qty">quantity</div>
          <div class="totalBox">total</div>            
        </div>

        <!-- 如果$store.state.userData不是null的時候，再顯示此區塊
        所以完全載入此頁面包含Header的時候，Header會將firebase資料更新到vuex
        這裡會接收到$store.state.userData不為null，所以渲染此區塊出來 -->
        <ul class="productsListBox" v-if="$store.state.userData !== null">
          <li class="items" v-for="item in $store.state.userData.testID.cartList" :key="item.pID">
            <div class="halfBox">
              <div class="imgBox">
                <img :src="item.imgSrc" alt="">
              </div>
              <div class="title">{{ item.title }}</div>
            </div>
            <div class="price">{{ '$ ' + item.price }}</div>
            <div class="qty">
              <input type="number" min="1" max="99" oninput="this.value=(this.value < Number(this.min) || this.value > Number(this.max))  ? '1' : this.value;" v-model="item.qty">
            </div>
            <div class="totalBox">
              <!-- 單一商品的數量*價格，取小數點後兩位 -->
              <span class="total">{{ '$ ' + (item.price * item.qty).toFixed(2) }}</span>
              <span class="delete" @click="deleteCartItem(item.pID)">×</span>
            </div>
          </li>
          <li class="couponBox">
            <div class="coupon" :class="{show: couponChecking}">
              <input type="text" placeholder="Your coupon code" v-model="coupon">
              <button type="submit" @click="checkCoupon">apply</button>
            </div>
          </li>
        </ul>
      </div>
      <div class="finalInfoBox">
        <div class="shipping">
          <div class="title">caculate shipping</div>
          <ul>
            <li>
              <select name="county" id="county" :class="{'invalid' : countyInvalid}" v-model="userAddress.county"></select>
              <span>請選擇縣市</span>
            </li>
            <li>
              <select name="district" id="district" :class="{'invalid' : districtInvalid}" v-model="userAddress.district"></select>
              <span>請選擇鄉鎮區域</span>
            </li>
            <li>
              <!-- 這個套件的郵遞區號不知道為什麼v-model雙向綁定就不會自動帶入，有時間再研究一下 -->
              <input id="zipCode" type="text" size="3" readonly placeholder="郵遞區號">
            </li>
            <li>
              <input id="address" type="text" size="3" :class="{'invalid' : addressInvalid}" placeholder="地址" v-model="userAddress.address">
              <span>請輸入完整地址</span>
            </li>
            <li class="submitBox">
              <button class="addressClear" @click="resetAddress">reset</button>
            </li>                
          </ul>
        </div>
        <div class="cartTotal">
          <div class="title">cart total</div>
          <ul>
            <li class="subtotal">
              <span class="subTitle">cart subtotal</span>
              <span class="price" v-if="$store.state.userData !== null">{{ '$ ' + subTotalSum }}</span>
            </li>
            <li class="shipping">
              <span class="subTitle">shipping</span>
              <span class="price" v-if="$store.state.userData !== null">{{ '$ ' + shippingFeed }}</span>
            </li>
            <li class="fanalSubtotal">
              <span class="subTitle">cart total</span>
              <span class="price" v-if="$store.state.userData !== null">{{ '$ ' + (subTotalSum + shippingFeed).toFixed(2) }}</span>
            </li>              
          </ul>
          <div class="submitBox text-right">
            <button class="addressConfirm" @click="checkAddress">confirm</button>
            <!-- <router-link class="checkOut" to="/checkOut">proceed to checkout</router-link> -->
          </div>
        </div>
      </div>
