  </div>

  <!-- 利用是否有orderInfo(結帳資料)來判斷要顯示的組件是shoppingCart或是checkOut -->
  <!-- 利用Props從Shopping-cart組件傳送subTotalSum的計算結果給子組件Checkout -->
  <checkOut v-if="orderInfo !== ''" :subTotal='subTotalSum' :shipping='shippingFeed'></checkOut>
  <!-- <checkOut :subTotal='subTotalSum' :shipping='shippingFeed'></checkOut> -->

  <Footer></Footer>
</template>


<script>
import Header from '@/components/Header.vue'
import Footer from '@/components/Footer.vue'
import checkOut from '@/views/Checkout.vue'
import TwCitySelector from 'tw-city-selector/dist/tw-city-selector.js';



export default {
  components: {
    Header,
    Footer,
    checkOut
  },
  data(){
    return {
      coupon: '',
      couponChecking: false,
      userAddress: {
        'county': '',
        'district': '',
        'zipCode': '',
        'address': '',
      },
      countyInvalid: false,
      districtInvalid: false,
      addressInvalid: false,
      orderInfo: '',
      // userData: [],
    };
  },
  methods: {
    test(){
    console.log(this.$store.state.userData)
    console.log(this.userData)
    // return this.$store.state.userData.testID;
      // this.userData = this.$store.state.userData.testID;
    },
    getData(){
      this.userData = this.$store.state.userData.testID;
    },
    deleteCartItem(pID) {
      // cartList是物件
      // 被點擊的按鈕會傳來該商品的pID索引值
      // 使用delete指令，刪除物件使用索引值進行刪除
      // delete this.userData.cartList[`${pID}`]
      this.$store.commit('deleteItem', pID)
    },
    checkCoupon(){
      this.couponChecking = true;
      setTimeout(() => {this.couponChecking = false}, 3000);
      // send coupon data to server
      // await server return result

      this.coupon = ''
    },
    resetAddress(){
      this.userAddress.county = ''
      this.userAddress.district = ''
      document.getElementById('zipCode').value = '';
      this.userAddress.address = ''
    },
    checkAddress(){
      this.countyInvalid = false;
      this.districtInvalid = false;
      this.addressInvalid = false;

      if (this.userAddress.county == '') {
        this.countyInvalid = true;
      } else if (this.userAddress.district == '') {
        this.districtInvalid = true;
      } else if (this.userAddress.address == '') {
        this.addressInvalid = true;
      } else {
        let fullAddress = 
        document.getElementById('zipCode').value + ' ' +
        this.userAddress.county + 
        this.userAddress.district + 
        this.userAddress.address;

        this.orderInfo = fullAddress + ', 總計：' + this.subTotalSum;
        console.log(this.orderInfo)
      }
    },
  },
  computed: {
    subTotalSum(){
      // 從vuex取得目前購物車狀態，並配合v-if讓vuex有值時才渲染DOM出來
      let subTotal = 0;
      for (let item in this.$store.state.userData.testID.cartList) {
        subTotal += this.$store.state.userData.testID.cartList[item].price * 
        this.$store.state.userData.testID.cartList[item].qty;
      }
      return subTotal;
    },
    shippingFeed(){
      let shippingFeed = 60;
      if (this.subTotalSum > 399) {
        shippingFeed = 0;
      }
      return shippingFeed;
    },
  },
  created(){
  },
  mounted(){

    new TwCitySelector({
      el: '.shipping',
      elCounty: '#county',
      elDistrict: '#district',
      elZipcode: '#zipCode' ,
      hasZipcode: true
    });


  },
};
</script>

